// client/src/components/ServiceDetailPage.js
import React from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import styled from 'styled-components';

// Funo para remover acentos
const removeAccents = (str) => {
  return str.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
};

// Estilizao usando styled-components
const Container = styled.div`
  max-width: 800px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;

  &:hover {
    transform: scale(1.02);
  }
`;

const BackButton = styled.button`
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.5rem;
  color: #2980b9;
  margin-bottom: 1rem;

  &:hover {
    color: #1a5276;
  }
`;

const Title = styled.h1`
  font-size: 2.5rem;
  color: #2c3e50;
  margin-bottom: 1rem;
  text-align: center; /* Centraliza o ttulo */
  text-transform: capitalize; /* Apenas a primeira letra de cada palavra em maiscula */
`;

const Description = styled.p`
  font-size: 1.2rem;
  color: #34495e;
  margin-bottom: 1.5rem;
`;

const SectionTitle = styled.h2`
  font-size: 2rem;
  color: #2980b9;
  margin-top: 2rem;
  margin-bottom: 1rem;
  text-transform: capitalize; /* Apenas a primeira letra de cada palavra em maiscula */
`;

const Details = styled.p`
  font-size: 1rem;
  color: #7f8c8d;
  line-height: 1.6;
`;

const BenefitsList = styled.ul`
  list-style-type: disc;
  margin-left: 1.5rem;
  color: #34495e;
`;

const BenefitItem = styled.li`
  margin-bottom: 0.5rem;
`;

const IconContainer = styled.div`
  font-size: 3rem; /* Tamanho do cone */
  text-align: center; /* Centraliza o cone */
  margin-bottom: 1rem; /* Espaamento abaixo do cone */
  color: #2980b9; /* Cor azul para os cones */
`;

const ServiceDetailPage = () => {
  const { title } = useParams(); // Obter o ttulo do servio da URL
  const navigate = useNavigate(); // Hook para navegao

  // Aqui voc pode buscar os detalhes do servio com base no ttulo
  const services = [
    {
      title: "odontologia-geral",
      description: "Tratamentos completos para manter a sua sade bucal em dia.",
      details: "A odontologia geral abrange uma variedade de servios, incluindo limpeza, restauraes, extraes e exames regulares. Nossos dentistas esto equipados para lidar com todas as suas necessidades de sade bucal.",
      benefits: [
        "Preveno de doenas bucais",
        "Melhoria da sade geral",
        "Tratamentos personalizados"
      ],
      additionalInfo: "A consulta regular com um dentista  essencial para manter a sade bucal. Recomendamos visitas a cada seis meses.",
      icon: "fas fa-tooth",
    },
    {
      title: "ortodontia",
      description: "Correo do alinhamento dos dentes.",
      details: "A ortodontia  especializada na correo de dentes e mandbulas desalinhados. Usamos aparelhos modernos e discretos para garantir que voc tenha um sorriso bonito e saudvel.",
      benefits: [
        "Melhora na esttica do sorriso",
        "Facilita a higiene bucal",
        "Reduz o risco de problemas dentrios futuros"
      ],
      additionalInfo: "Os tratamentos ortodnticos podem durar de 6 meses a 2 anos, dependendo da complexidade do caso.",
      icon: "fas fa-teeth",
    },
    {
      title: "implantes-dentarios",
      description: "Substitua dentes perdidos com implantes.",
      details: "Os implantes dentrios so uma soluo permanente para dentes perdidos. Eles so projetados para se parecer e funcionar como dentes naturais, proporcionando conforto e funcionalidade.",
      benefits: [
        "Soluo duradoura",
        "Melhora na mastigao",
        "Aumenta a autoestima"
      ],
      additionalInfo: "Os implantes so feitos de titnio, um material biocompatvel que se integra ao osso.",
      icon: "fas fa-teeth-open",
    },
    {
      title: "endodontia",
      description: "Tratamento de canal especializado.",
      details: "A endodontia  o tratamento de problemas relacionados  polpa dentria. Nossos especialistas garantem que voc esteja confortvel durante todo o processo.",
      benefits: [
        "Alvio da dor",
        "Preservao do dente natural",
        "Tratamento eficaz"
      ],
      additionalInfo: "O tratamento de canal pode salvar um dente que, de outra forma, precisaria ser extrado.",
      icon: "fas fa-pump-medical",
    },
    {
      title: "estetica-dental",
      description: "Procedimentos para melhorar a aparncia do seu sorriso.",
      details: "A esttica dental inclui uma variedade de tratamentos, como clareamento, facetas e restauraes estticas, para ajudar voc a alcanar o sorriso dos seus sonhos.",
      benefits: [
        "Sorriso mais brilhante",
        "Aumento da confiana",
        "Resultados personalizados"
      ],
      additionalInfo: "Os tratamentos estticos so personalizados para atender s suas necessidades e desejos especficos.",
      icon: "fas fa-smile",
    },
    {
      title: "odontopediatria",
      description: "Cuidados dentrios especializados para crianas.",
      details: "A odontopediatria  focada na sade bucal de crianas e adolescentes. Criamos um ambiente acolhedor e divertido para que as crianas se sintam confortveis durante as consultas.",
      benefits: [
        "Cuidados preventivos",
        "Educao sobre higiene bucal",
        "Tratamentos adaptados para crianas"
      ],
      additionalInfo: "As consultas regulares ajudam a estabelecer hbitos saudveis desde cedo.",
      icon: "fas fa-baby",
    }
  ];

  // Verifique se o ttulo est correto e faa a busca
  const service = services.find(s => removeAccents(s.title) === removeAccents(title)); // Encontrar o servio pelo ttulo

  return (
    <Container>
      <BackButton onClick={() => navigate(-1)}>&larr; Voltar</BackButton> {/* Boto de voltar */}
      {service ? (
        <>
          <IconContainer>
            <i className={service.icon}></i> {/* Renderizando o cone como uma classe */}
          </IconContainer>
          <Title>{service.title.replace(/-/g, ' ')}</Title> {/* Ttulo formatado */}
          <Description>{service.description}</Description>
          <SectionTitle>Detalhes</SectionTitle>
          <Details>{service.details}</Details>
          <SectionTitle>Benefcios</SectionTitle>
          <BenefitsList>
            {service.benefits.map((benefit, index) => (
              <BenefitItem key={index}>
                <i className={service.icon}></i> {benefit} {/* Renderizando o cone associado ao servio */}
              </BenefitItem>
            ))}
          </BenefitsList>
          <SectionTitle>Informaes Adicionais</SectionTitle>
          <Details>{service.additionalInfo}</Details>
        </>
      ) : (
        <p>Servio no encontrado.</p>
      )}
    </Container>
  );
};

export default ServiceDetailPage;